import { action } from "@storybook/addon-actions";
import type { Meta, StoryObj } from "@storybook/react";
import { FakeCameraVideoTrack, FakeMicrophoneAudioTrack, FakeRTCClient } from "fake-agora-rtc";
import { useEffect, useMemo, useState } from "react";
import { AgoraRTCProvider } from "../hooks";
import type { LocalMicrophoneAndCameraUserProps } from "./LocalMicrophoneAndCameraUser";
import { LocalUser } from "./LocalUser";
const meta: Meta<LocalMicrophoneAndCameraUserProps> = {
  title: "User/LocalUser",
  component: LocalUser,
  tags: ["autodocs"],
  parameters: {
    backgrounds: {
      default: "light"
    }
  }
};
export default meta;
export interface OverviewProps {
  micOn: boolean;
  cameraOn: boolean;
}
type OverviewArgs = OverviewProps & Omit<LocalMicrophoneAndCameraUserProps, keyof OverviewProps>;
export const Overview: StoryObj<OverviewArgs> = {
  args: {
    micOn: false,
    cameraOn: false,
    playVideo: false,
    playAudio: false,
    cover: "https://www.agora.io/en/wp-content/uploads/2022/10/3d-spatial-audio-icon.svg",
    style: {
      width: 288,
      height: 216
    }
  },
  render: function RenderLocalUser({
    micOn,
    cameraOn,
    ...args
  }: OverviewArgs) {
    const [client] = useState(() => FakeRTCClient.create({
      publish: async () => {
        action("IAgoraRTCClient.publish()")();
      }
    }));
    const audioTrack = useMemo(() => {
      return micOn ? FakeMicrophoneAudioTrack.create() : null;
    }, [micOn]);
    const videoTrack = useMemo(() => {
      return cameraOn ? FakeCameraVideoTrack.create() : null;
    }, [cameraOn]);
    useEffect(() => {
      if (client && audioTrack) {
        client.publish(audioTrack);
      }
    }, [client, audioTrack]);
    useEffect(() => {
      if (client && videoTrack) {
        client.publish(videoTrack);
      }
    }, [client, videoTrack]);
    return <AgoraRTCProvider client={client}>
        <LocalUser audioTrack={audioTrack} cameraOn={cameraOn} micOn={micOn} videoTrack={videoTrack} {...args} />
      </AgoraRTCProvider>;
  }
};
Overview.parameters = {
  ...Overview.parameters,
  docs: {
    ...Overview.parameters?.docs,
    source: {
      originalSource: "{\n  args: {\n    micOn: false,\n    cameraOn: false,\n    playVideo: false,\n    playAudio: false,\n    cover: \"https://www.agora.io/en/wp-content/uploads/2022/10/3d-spatial-audio-icon.svg\",\n    style: {\n      width: 288,\n      height: 216\n    }\n  },\n  render: function RenderLocalUser({\n    micOn,\n    cameraOn,\n    ...args\n  }: OverviewArgs) {\n    const [client] = useState(() => FakeRTCClient.create({\n      publish: async () => {\n        action(\"IAgoraRTCClient.publish()\")();\n      }\n    }));\n    const audioTrack = useMemo(() => {\n      return micOn ? FakeMicrophoneAudioTrack.create() : null;\n    }, [micOn]);\n    const videoTrack = useMemo(() => {\n      return cameraOn ? FakeCameraVideoTrack.create() : null;\n    }, [cameraOn]);\n    useEffect(() => {\n      if (client && audioTrack) {\n        client.publish(audioTrack);\n      }\n    }, [client, audioTrack]);\n    useEffect(() => {\n      if (client && videoTrack) {\n        client.publish(videoTrack);\n      }\n    }, [client, videoTrack]);\n    return <AgoraRTCProvider client={client}>\n        <LocalUser audioTrack={audioTrack} cameraOn={cameraOn} micOn={micOn} videoTrack={videoTrack} {...args} />\n      </AgoraRTCProvider>;\n  }\n}",
      ...Overview.parameters?.docs?.source
    }
  }
};